import Link from 'next/link'
import { useEffect, useState } from 'react'

export default function Navbar() {
  const [theme, setTheme] = useState('light');

  useEffect(() => {
    const saved = localStorage.getItem("ft_theme");
    if (saved) setTheme(saved);
    else {
      const prefersDark = window.matchMedia("(prefers-color-scheme: dark)").matches;
      setTheme(prefersDark ? "dark" : "light");
    }
  }, []);

  const toggleTheme = () => {
    const next = theme === "dark" ? "light" : "dark";
    setTheme(next);
    localStorage.setItem("ft_theme", next);
    document.documentElement.classList.toggle("dark", next === "dark");
  };

  return (
    <nav className="w-full py-4 px-6 flex items-center justify-between sticky top-0 z-30 backdrop-blur-md bg-white/30 dark:bg-black/30">
      <div className="flex items-center gap-3">
        <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-ft-blue to-ft-teal flex items-center justify-center text-white font-bold shadow">FT</div>
        <div className="font-semibold text-lg">FitTrack Pro</div>
      </div>

      <div className="flex items-center gap-3">
        <Link href="/dashboard" className="px-3 py-2 rounded hover:bg-slate-200/50 dark:hover:bg-white/10">Dashboard</Link>
        <Link href="/weekly-plan" className="px-3 py-2 rounded hover:bg-slate-200/50 dark:hover:bg-white/10">Plan</Link>
        <Link href="/onboard/goal" className="btn-primary">Create Plan</Link>
        <button onClick={toggleTheme} className="small-btn">{theme === "dark" ? "üåô" : "‚òÄÔ∏è"}</button>
      </div>
    </nav>
  );
}
